export const fetchData = async (tableName) => {
  const response = await fetch(process.env.REACT_APP_URL_FETCH_DATA, {
    method: "POST",
    body: JSON.stringify({tableName}),
    headers: { "api-key": process.env.REACT_APP_API_KEY }
  });
  return await response.json();
};

export const fetchById = async (tableName, id) => {
  const response = await fetch(process.env.REACT_APP_URL_FETCH_BY_ID, {
    method: "POST",
    body: JSON.stringify({ tableName, id }),
    headers: { "api-key": process.env.REACT_APP_API_KEY },
  });
  return await response.json();
};
